import React from "react";
const useCheckout=async(cbInstance,queryparam,plan_id)=>{
    const { domain, channel,mode } = queryparam;
    console.log("queryparam",queryparam);
    const basePath = window.location.origin;
    let API_BASE_URL = process.env.REACT_APP_API_BASE_URL_LIVE;
    if( mode == '_sandbox'){
      API_BASE_URL = process.env.REACT_APP_API_BASE_URL_SANDBOX
    }
    let response = await fetch(API_BASE_URL+"/ChargebeeCheckout", {
      method: "POST",
      body: JSON.stringify({ plan_id: plan_id, domain: domain, channel: channel,mode:mode }),
    })
    let data = await response.json();
    if (data?.status == true) {
      // window.location.href = data?.hostedpage;
      try {
        cbInstance.openCheckout({
          hostedPage: async() => {
           return await { url: data?.hostedpage };
          },
          success: async(hostedPageId) => {
            window.location.href = basePath+"/pricing?mode="+mode+"&domain="+domain+"&channel="+channel+"&hostedpageId="+hostedPageId;
          }
          ,
          close: () => {
            console.log('Checkout was closed');
          }
        });
      } catch (error) {
        console.error('Error fetching hosted page:', error);
      }
    }
    return data;
 }
export default useCheckout;