import {useEffect, useRef, useState} from 'react';
import { Flex, Progress } from 'antd';
import { useNavigate } from 'react-router-dom';
import { Button, message, Popconfirm } from 'antd';
import LoadingScreen from './LoadingScreen';
const ExistingPlan = ({updateplan,existplan,queryparam}) => {
    const navigate = useNavigate();
    const[exitdata,setexistdata]=useState({});
    const[loading,setloading]=useState(false);
    const handleClick=()=>{
        updateplan(); 
    }
    const cancleplan=async()=>{
     
      setloading(true);
      const{domain,channel,mode} = queryparam;
      const {subscription_id,status} = existplan;
      let API_BASE_URL = process.env.REACT_APP_API_BASE_URL_LIVE;
      if( mode == '_sandbox'){
        API_BASE_URL = process.env.REACT_APP_API_BASE_URL_SANDBOX
      }
      let response = await fetch(API_BASE_URL+"/deleteChargebeeSubscription", {
        method: "POST",
        body: JSON.stringify({ subscription_id: subscription_id, domain: domain, channel: channel,mode:mode }),
      })
      let data = await response.json();
      if(data?.status==true){
        setTimeout(()=>{
          setloading(false);
          window.location.reload();
        },10000)
      }
    }
    useEffect(()=>{
      if(existplan?.plan_name=="Free"){
        setexistdata({
          next_payment_date:'-',
          end_date:'-',
          Name:existplan?.plan_name,
          billing_period:'-',
          price:0,
          status:existplan?.status,
          allowed_orders:existplan?.allowed_orders,
          allowed_products:existplan?.allowed_products
      });
      }else{
        const {allowed_products='',allowed_orders=''} = existplan
          const{billing_intents={},next_payment_date='',end_date='',status=''}=existplan?.data;
          const{Name,billing_period,price}=billing_intents;
          setexistdata({
              next_payment_date:next_payment_date,
              end_date:end_date,
              Name:Name,
              billing_period:billing_period,
              price:price,
              status:status,
              allowed_products:allowed_products,
              allowed_orders:allowed_orders
          });
      }
    },[])

    const confirm =()=>{
      cancleplan();
    }
    return (
        <>
    {loading && <LoadingScreen/>}
    <div className="ced-pricing-plan-wrapper">
      <div className="ced-pricing-plan-wrap">
        <div className="ced-pricing-plan-container">
          <div className="ced-pricing-plan-common-wrapper">
            <h3>Current Plan Details</h3>
          </div>
          <div className="ced-pricing-plan-details-container">
            <div className="ced-pricing-plan-details-wrap">
              <table>
                <tbody>
                  <tr>
                    <td>Plan Status</td>
                    <td>:</td>
                    <td><span className="ced-active">{exitdata?.status?.toUpperCase()}</span></td>
                  </tr>
                  <tr>
                    <td>Plan Name</td>
                    <td>:</td>
                    <td>{exitdata?.Name}</td>
                  </tr>
                  <tr>
                    <td>Plan Price</td>
                    <td>:</td>
                    <td>${exitdata?.price/100}</td>
                  </tr>
                  <tr>
                    <td>Billing Period</td>
                    <td>:</td>
                    <td>{exitdata?.billing_period?.toUpperCase()}</td>
                  </tr>
                  <tr>
                    <td>End Date</td>
                    <td>:</td>
                    <td>{exitdata?.end_date}</td>
                  </tr>
                  <tr>
                    <td>Next Payment Date</td>
                    <td>:</td>
                    <td>{exitdata?.next_payment_date}</td>
                  </tr>
                  <tr>
                    <td>Allowed Order</td>
                    <td>:</td>
                    <td>{exitdata?.allowed_orders}</td>
    
                  </tr>
                  <tr>
                    <td>Allowed Products</td>
                    <td>:</td>
                    <td>{exitdata?.allowed_products}</td>
                    <td>
                  </td>
                  </tr>
                </tbody>
              </table>
            </div>
            <div className="ced-pricing-plan-action-container">
              <div className="ced-pricing-action-buttons">
              
                <button className="ced-update" onClick={handleClick}>Update</button>
                {
                  exitdata?.status != "non_renewing" && exitdata?.Name != "Free" && 
                  <Popconfirm
                  title="Delete the Subscription"
                  description="Are you sure to delete this Subscription?"
                  onConfirm={confirm}
                >
                  <button className="ced-cancel">Cancel</button>
                </Popconfirm>
                  
                  // <button className="ced-cancel" onClick={cancleplan}>Cancel</button>
                }
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <Popconfirm
                  title="Delete the task"
                  description="Are you sure to delete this task?"
                  
                ></Popconfirm>
        </>
    )
}
export default ExistingPlan;