import { useRef, useEffect, useState } from "react";
import { Button, Result } from 'antd';
const FreePlan = ({ showfreeplan, queryparam }) => {
   // const API_BASE_URL = process.env.REACT_APP_API_BASE_URL_LIVE;
    const [emailsent, setemailsent] = useState(false);
    const [emailmess, setemailmess] = useState('');
    const [emailerror, setemailerror] = useState('');
    const[emailProcess,setemailProcess] = useState(false);
    const emailref = useRef();
    const { domain, channel ,mode } = queryparam;
    const remindmelater = () => {
        showfreeplan(false)
    }
    const overlay = () => {
        showfreeplan(false)
    }
    const verifyemail = async () => {
        let API_BASE_URL = process.env.REACT_APP_API_BASE_URL_LIVE;
        if( mode == '_sandbox'){
            API_BASE_URL = process.env.REACT_APP_API_BASE_URL_SANDBOX
        }
        setemailProcess(true);
        let email = emailref.current.value;
        let valid = validateEmail(email)
        if (valid) {
            try{
            let response = await fetch(API_BASE_URL+"/verifyemail?domain=" + domain + "&channel=" + channel + "&mode="+mode+"&email=" + email)
            let result = await response.json();

                if (result?.status == true) {

                    setemailsent(!emailsent);
                    setemailmess(result?.message);
                }
            } catch (error){
                setemailerror("Something went wrong");
                setemailProcess(false);
                console.error('Error fetching data:', error);
            }
        }else{
            setemailerror("Invalid E-mail! , Please Enter valid E-mail")
            setemailProcess(false);
        }

    }
    function validateEmail(email) {
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        return emailRegex.test(email);
    }
    const pickemail=()=>{
        setemailerror('')
    }
    return (
        <form>

            <div className="ced-popup-form-wrapper">
                <div className="ced-poup-form-content">
                    <div id="ced-popup-form" className="ced-popup-overlay">
                        <div className="ced-popup-wrap">
                            {
                                emailsent ?
                                    <Result
                                        status="success"
                                        title="Verify Your Email Address"
                                        subTitle={emailmess}
                                        extra={[
                                            <Button onClick={remindmelater} key="buy">Close</Button>,
                                        ]}
                                    />
                                    :
                                    <>
                                        <h2>Start Your Free Plan! </h2>
                                        <div className="ced-popup-content-wrapper">
                                            <p>Please provide your email address to get started with your free plan.</p>
                                        </div>
                                        <div className="ced-popup-form-wrap">
                                            <div className="ced-form-wrapper-popup">
                                                <input type="email" id="email" name="mbc_email" ref={emailref} onChange={pickemail} placeholder="Email Address" required />
                                                <span style={{fontSize:"10px",color:"red"}}>{emailerror}</span>
                                                {
                                                    emailProcess ? <Button type="button" loading name="ced_mbc_submit_email" className="ced-popup-form-button" value="Submit"></Button>
                                                    :  <Button type="button" name="ced_mbc_submit_email" onClick={verifyemail} className="ced-popup-form-button" value="Submit">verify</Button>
                                                }
                                            </div>
                                            <div className="ced-popup-fomr-steps">
                                                <span className="ced-popup-close" onClick={remindmelater}>Remind me later</span>
                                            </div>
                                        </div>
                                    </>
                            }
                        </div>
                    </div>
                </div>
            </div>
        </form>
    )
}
export default FreePlan;