import { Spin } from 'antd';
import { useEffect } from 'react';
import { useLocation } from "react-router-dom";
import LoadingScreen from './LoadingScreen';
const Success = () => {
    const location = useLocation();
    //const API_BASE_URL = process.env.REACT_APP_API_BASE_URL_LIVE;
    const gettoken=()=>{
        const data = location?.state;
        const{domain,channel,mode}=data;
        let API_BASE_URL = process.env.REACT_APP_API_BASE_URL_LIVE;
        if( mode == '_sandbox'){
        API_BASE_URL = process.env.REACT_APP_API_BASE_URL_SANDBOX
        }
    //   const {domain , channel } = query.get('query');
        setTimeout(() => {
            const gettoken = async () => {
                let response = await fetch(API_BASE_URL+"/getaccesstoken?domain=" + domain + "&mode="+mode+"&channel=" + channel + "")
                let data = await response?.json();
                console.log("data---",data);
                let token = data?.access_token;
                   // Data to send
               const tokenmess = {
                status: true,
                token: token
            };
            window.parent.postMessage(tokenmess, '*');
            // Send data to the parent window
            
               // navigate("/pricing");
              // parent.location.href = domain+"/wp-admin/admin.php?page=sales_channel&channel=shopee&token="+token;
            }
            gettoken();
        }, 5000)
    }
    useEffect(()=>{
        gettoken();
    },[])
    return (
        <div>
            <LoadingScreen />
        </div>
    )
}
export default Success;