const useUserGet=async({domain,channel,mode})=>{
    let API_BASE_URL = process.env.REACT_APP_API_BASE_URL_LIVE;
    if( mode == '_sandbox'){
      API_BASE_URL = process.env.REACT_APP_API_BASE_URL_SANDBOX
    }
    const requestOptions = {
        method: "GET",
        redirect: "follow"
      };
      
      let response = await fetch(API_BASE_URL+"/getUser?domain="+domain+"&channel="+channel+"&mode="+mode, requestOptions)
      let users = await response.json();
     return users;
    }
export default useUserGet;