const useChargebee = () => {
  let cbInstance;
  const query = new URLSearchParams(window.location.search);
        const mode = query.get('mode');
        let site = "cedcommerce";
        if(mode == "_sandbox"){
          site = "cedcommerce-test";
        }
    const loadChargebee = () => {
      if (window.Chargebee) {
        cbInstance = window.Chargebee.init({
          site: site
        });
      }
    };

    if (document.readyState === 'complete') {
      loadChargebee();
    } else {
      window.addEventListener('load', loadChargebee);
      return () => window.removeEventListener('load', loadChargebee);
    }

  return cbInstance;
};
export default useChargebee;
