const useUserPost=({domain,channel,mode})=>{
  let API_BASE_URL = process.env.REACT_APP_API_BASE_URL_LIVE;
  if( mode == '_sandbox'){
    API_BASE_URL = process.env.REACT_APP_API_BASE_URL_SANDBOX
  }
    const requestOptions = {
        method: "GET",
        redirect: "follow"
      };
      
      fetch(API_BASE_URL+"/addUser?domain="+domain+"&channel="+channel+"&mode="+mode, requestOptions)
        .then((response) => response.text())
        .then((result) => console.log(result))
        .catch((error) => console.error(error));
}
export default useUserPost; 