
const useGetExistingPlan=async({domain,channel,mode})=>{
  
        let API_BASE_URL = process.env.REACT_APP_API_BASE_URL_LIVE;
    
        if( mode == '_sandbox'){
        API_BASE_URL = process.env.REACT_APP_API_BASE_URL_SANDBOX
        }
        const requestOptions = {
            method: "GET",
            redirect: "follow"
        };
        try{
        let response = await fetch(API_BASE_URL+"/getSubscription?domain="+domain+"&channel="+channel+"&mode="+mode, requestOptions)
        let subscription = await response?.json();
        return subscription;

        }catch(e){
            
           // console.error(e);

        }
    }
export default useGetExistingPlan;