const useGetPlans=async({domain,channel,mode})=>{
   let API_BASE_URL = process.env.REACT_APP_API_BASE_URL_SANDBOX;
  
   let responce  = await fetch(API_BASE_URL+"/subscription?action=pricing_plans&channel=woocommerce_sea");
   let data =    await responce?.json();
   return data?.data?.woocommerce_sea;
}
export default useGetPlans;